.SignUpPage {
    height: 450px;
    width: 355px;
    margin-top: 130px;
    margin-left: 565px;
    background: -webkit-radial-gradient(rgba(255, 255, 255, 0.479),rgba(128, 128, 128, 0.445))no-repeat fixed;
    background: -o-radial-gradient(rgba(255, 255, 255, 0.479),rgba(128, 128, 128, 0.445))no-repeat fixed;
    background: radial-gradient(rgba(255, 255, 255, 0.479),rgba(128, 128, 128, 0.445))no-repeat fixed;
    border: rgba(0, 0, 0, 0.438) 1px dotted;
    text-align: center;
    padding-right: 30px;
    padding-top: 45px;
    border-radius: 15px;
    -webkit-box-shadow: black 0px 0px 1px 0px;
            box-shadow: black 0px 0px 1px 0px;
}

.SignUpPage > img {
    width:150px;
    position: relative;
    top: -20px;
    right: -10px;
}


