body {
  background: -webkit-gradient( linear,left top, left bottom,color-stop(1%, rgba(0, 0, 0, 0.411)), to(rgb(182, 182, 250)))no-repeat fixed;
  background: -webkit-linear-gradient( top,rgba(0, 0, 0, 0.411) 1%, rgb(182, 182, 250))no-repeat fixed;
  background: -o-linear-gradient( top,rgba(0, 0, 0, 0.411) 1%, rgb(182, 182, 250))no-repeat fixed;
  background: linear-gradient( 180deg,rgba(0, 0, 0, 0.411) 1%, rgb(182, 182, 250))no-repeat fixed;
  background-repeat: no-repeat;
  background-position: center; 
  background-attachment: fixed;
}

#geneva {
  position: absolute;
  z-index: -2;
  width: 700px;
  /* margin-left: 400px; */
  margin: -110px 0px 0px 370px;
}


